import React, {Component} from 'react';
import Body from './Body/Body'
import Logo from './react-img/Logo.png';
import heart from './react-img/Menu_heart.png';
import Do from './What_do_we_do/index';
import Works from './Awsome_Works/index';
import Journal from './Journal/index';
import Footer from './Footer/index';
import HeaderText from './HeaderText/index';

class App extends Component {
    constructor(props) {
        super(props);
        this.change = this.change.bind(this);
        this.state = {
            body: <Body/>
        }
    }
    change(props) {
        switch (props) {
            case 'home': this.setState({body: <Body/>});
            break;
            case 'about': this.setState({body: <Do/>});
            break;
            case 'work': this.setState({body: <Works/>});
            break;
            case 'journal': this.setState({body: <Journal/>});
            break;
            case 'contact': this.setState({body:
                <div className="d-flex justify-content-center mt-5">
                    <div className="border border-dark p-5">
                        <h2 className="font-bebas">Contact Us</h2>
                        <input className="m-3" type="text" placeholder="Your name"/> <br/>
                        <input className="m-3" type="text" placeholder="Your email"/> <br/>
                        <input className="m-3" type="text" placeholder="Your phone"/> <br/>
                        <br/>
                        <button className="float-right send_btn font-bebas">Send</button>
                    </div>
                </div>});
        }
    }
    render() {
        return (
            <div>
                <div className="container">
                    <div className={'header row mt-5'}>
                        <a href={'/home'} className="col-6 logo">
                            <img src={Logo} alt=""/>
                        </a>
                        <div className={'col-6 navigation'}>
                            <div className="row">
                                <div className="col-2 pt-2"><a onClick={(props)=> this.change('home')}>HOME</a></div>
                                <div className="col-2 pt-2"><a onClick={(props)=> this.change('about')}>ABOUT US</a></div>
                                <div className="col-2 pt-2"><a onClick={(props)=> this.change('work')}>OUR WORKS</a></div>
                                <div className="col-2 pt-2"><a onClick={(props)=> this.change('journal')}>JOURNAL</a></div>
                                <div className="col-2 pt-2"><a onClick={(props)=> this.change('contact')}>CONTACT</a></div>
                                <div className="col-2">
                                    <img src={heart} alt=""/>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <HeaderText/>
                <div className="container">
                    {this.state.body}
                </div>
                <div className={"block4"}>
                    <Footer/>
                </div>
            </div>
        )
    }
}
export default App


