import React from 'react';
import './style.css';

function JournalCard({obj}) {
    return(
        <div className={"row mt-5"}>
            <div className="col-5">
                <img src={obj.img} alt=""/>
            </div>
            <div className="col-7">
                <p className={"date font-bebas"}>{obj.date}</p>
                <p className={"title font-bebas"}>{obj.title}</p>
                <p className={"text mt-1 font-arial"}>{obj.text}</p>
            </div>
        </div>
    )
}




export default JournalCard


