import React from 'react';
import './style.css';
import JournalCard from './JournalCard/index';
import img1 from '../react-img/journal/Layer_10.png';
import img2 from '../react-img/journal/Layer_11.png';
import img3 from '../react-img/journal/Layer_12.png';
import img4 from '../react-img/journal/Layer_13.png';

function Journal() {
    let first = {
        img: img1,
        date: "OCT 13, 2014  / IDENTITY",
        title: "SOLLICITUDIN TORTOR INCEPTOS",
        text: "Maecenas faucibus mollis interdum. Cum sociis natoque penatibus et magnis dis parturient montes, nascetur ridiculus mus. Nullam id dolor id nibh."
    };
    let second = {
        img: img2,
        date: "OCT 23, 2014  / BRANDING",
        title: "AMET INCEPTOS FRINGILLA FUSCE",
        text: "Maecenas faucibus mollis interdum. Cum sociis natoque penatibus et magnis dis parturient montes, nascetur ridiculus mus. Nullam id dolor id nibh."
    };
    let third = {
        img: img3,
        date: "SEP 19, 2014  /  IDENTITY",
        title: "CURSUS MALESUADA JUSTO",
        text: "Maecenas faucibus mollis interdum. Cum sociis natoque penatibus et magnis dis parturient montes, nascetur ridiculus mus. Nullam id dolor id nibh."
    };
    let fourth = {
        img: img4,
        date: "SEP 27, 2014  /  BRANDING",
        title: "ORNARE CURSUS BIBENDUM PURUS",
        text: "Maecenas faucibus mollis interdum. Cum sociis natoque penatibus et magnis dis parturient montes, nascetur ridiculus mus. Nullam id dolor id nibh."
    };
    return (
        <div className={"mt-5"}>
            <p className={"block3__title font-bebas"}>FROM THE JOURNAL --</p>
            <p className={"block3__text font-arial"}>Cum sociis natoque penatibus et magnis dis parturient montes, nascetur
                ridiculus mus. Donec ullamcorper nulla non metus auctor fringilla.</p>
            <div className="row">
                <div className="col-6">
                    <JournalCard obj={first}/>
                </div>
                <div className="col-6">
                    <JournalCard obj={second}/>
                </div>
            </div>
            <div className="row mt-3">
                <div className="col-6">
                    <JournalCard obj={third}/>
                </div>
                <div className="col-6">
                    <JournalCard obj={fourth}/>
                </div>
            </div>
        </div>
    )
}




export default Journal


