import React from 'react';
import Works from '../Awsome_Works/index';
import Do from '../What_do_we_do/index';
import Journal from '../Journal/index';

function Body() {
    return(
        <div>
            <div>

            </div>
            <div className="container mt-5">
                <div className="block1">
                    <Works/>
                </div>
                <div className="block2">
                    <Do/>
                </div>
                <div className="block3">
                    <Journal/>
                </div>
            </div>
        </div>
    )
}

export default Body


