import React from 'react';
import Modal from 'react-modal';
import btn from '../react-img/contact_btn.png';
import './style.css';

const customStyles = {
    content : {
        top                   : '50%',
        left                  : '50%',
        right                 : 'auto',
        bottom                : 'auto',
        marginRight           : '-50%',
        transform             : 'translate(-50%, -50%)'
    },
    overlay: {
        background: "rgba(0, 0, 0, 0.7)"
    }
};

class Test extends React.Component {
    constructor() {
        super();

        this.state = {
            modalIsOpen: false
        };

        this.openModal = this.openModal.bind(this);
        this.afterOpenModal = this.afterOpenModal.bind(this);
        this.closeModal = this.closeModal.bind(this);
    }

    openModal() {
        this.setState({modalIsOpen: true});
    }
    afterOpenModal() {
        //
    }
    closeModal() {
        this.setState({modalIsOpen: false});
    }

    render() {
        return (
            <div>
                <a onClick={this.openModal}>
                    <img src={btn} alt=""/>
                </a>
                <Modal
                    isOpen={this.state.modalIsOpen}
                    onAfterOpen={this.afterOpenModal}
                    onRequestClose={this.closeModal}
                    style={customStyles}
                    contentLabel="Example Modal">

                    <h2 className="font-bebas">Contact Us</h2>
                    <input className="m-3" type="text" placeholder="Your name"/> <br/>
                    <input className="m-3" type="text" placeholder="Your email"/> <br/>
                    <input className="m-3" type="text" placeholder="Your phone"/> <br/>
                    <br/>
                    <button className="send_btn font-bebas">Send</button>
                    <button className="font-bebas close_btn" onClick={this.closeModal}>Close</button>
                </Modal>
            </div>
        );
    }
}
export default Test


