import React from 'react';
import './style.css';
import facebook from '../react-img/Facebook.png'
import twitter from '../react-img/Twitter.png'
import rss from '../react-img/RSS.png'
import drible from '../react-img/Dribbble.png'
import pinterest from '../react-img/Pinterest.png'

function Footer() {
    return(
        <div className={"footer"}>
            <div className="footer__content d-flex justify-content-center">
                    <p className={"font-arial"}>© 2014 Keepsake. All rights reserved. Theme by elemis.</p>
            </div>
            <div className="d-flex justify-content-center">
                <img className={'pointer'} src={facebook} alt=""/>
                <img className="ml-3 pointer" src={twitter} alt=""/>
                <img className="ml-3 pointer" src={rss} alt=""/>
                <img className="ml-3 pointer" src={drible} alt=""/>
                <img className="ml-3 pointer" src={pinterest} alt=""/>
            </div>
        </div>
    )
}






export default Footer


