import React from 'react';
import './style.css'

import Card from './Card/index';
import img1 from '../react-img/what do we do/web desing.png';
import img2 from '../react-img/what do we do/tv.png';
import img3 from '../react-img/what do we do/print.png';
import img4 from '../react-img/what do we do/love.png';

function Do() {
    let first = {
        img: img1,
        title: "WEB DESIGN",
        text: "Donec elit non mi porta gravida eureget metus. Aenean eu leo quam. Pellentesque ornare sem por quam venenatis vestibulum."
    };
    let second = {
        img: img2,
        title: "MOTION VIDEO",
        text: "Aenean lacinia bibendum nulla sed consectetur. Nullam id dolor id nibh ultricies vehicula ut id elit. Sed posuere consectetur."
    };
    let third = {
        img: img3,
        title: "PRINT DESIGN",
        text: "Maecenas sed diam eget risus varius blandit amet non magna. Nullam id dolor id nibh ultricies vehiculaelit praesent commodo."
    };
    let fourth = {
        img: img4,
        title: "ILLUSTRATION",
        text: "Cum sociis natoque penatibus et  dis parturient montes, nascetur ridiculus mus. Morbi leo risus, porta ac consectetur ac at."
    };
    return (
        <div>
            <div className="block2__body mt-5">
                <p className={"block2__body__title font-bebas"}>WHAT DO WE DO --</p>
                <p className={"block2__body__text font-arial"}>Maecenas sed diam eget risus varius blandit
                    sit amet non magna. Morbi leo risus, porta ac consectetur ac, vestibulum at eros.</p>
                <div className="row mt-5">
                    <div className="col-3">
                        <Card obj={first}/>
                    </div>
                    <div className="col-3">
                        <Card obj={second}/>
                    </div>
                    <div className="col-3">
                        <Card obj={third}/>
                    </div>
                    <div className="col-3">
                        <Card obj={fourth}/>
                    </div>
                </div>
            </div>
        </div>
    )
}




export default Do


