import React from 'react';
import './style.css';
import img1 from '../react-img/awsome works/Layer_1.png';
import img2 from '../react-img/awsome works/Layer_2.png';
import img3 from '../react-img/awsome works/Layer_3.png';
import img4 from '../react-img/awsome works/Layer_4.png';
import img5 from '../react-img/awsome works/Layer_5.png';
import img6 from '../react-img/awsome works/Layer_6.png';
import img7 from '../react-img/awsome works/Layer_7.png';
import img8 from '../react-img/awsome works/Layer_8.png';
import img9 from '../react-img/awsome works/Layer_9.png';
import Modal from '../Modal_btn/Modal_btn';


function Works() {
    return (
        <div className={'block1__content mt-5'}>
            <p className={'block1__title font-bebas'}>OUR AWESOME WORKS</p>
            <div className={'block1__photoBlock'}>
                <div className="row margin">
                    <div className="col-4">
                        <img className={'block1__photo'} src={img1} alt=""/>
                    </div>
                    <div className="col-4">
                        <img className={'block1__photo'} src={img2} alt=""/>
                    </div>
                    <div className="col-4">
                        <img className={'block1__photo'} src={img3} alt=""/>
                    </div>
                </div>
                <div className="row margin">
                    <div className="col-4">
                        <img className={'block1__photo'} src={img4} alt=""/>
                    </div>
                    <div className="col-4">
                        <img className={'block1__photo'} src={img5} alt=""/>
                    </div>
                    <div className="col-4">
                        <img className={'block1__photo'} src={img6} alt=""/>
                    </div>
                </div>
                <div className="row margin">
                    <div className="col-4">
                        <img className={'block1__photo'} src={img7} alt=""/>
                    </div>
                    <div className="col-4">
                        <img className={'block1__photo'} src={img8} alt=""/>
                    </div>
                    <div className="col-4">
                        <img className={'block1__photo'} src={img9} alt=""/>
                    </div>
                </div>
            </div>
            <div className="block1__contact mt-5">
                <div className="block1__contact__title text-center font-bebas">
                    <p>WOULD YOU LIKE TO WORK WITH US?</p>
                    <Modal/>
                </div>
            </div>
        </div>
    )
}

export default Works


