import React from 'react';
import './style.css';

function Card({obj}) {
    return(
        <div className={'text-center'}>
            <img src={obj.img} alt=""/>
            <p className={'card__title font-bebas mt-3'}>{obj.title}</p>
            <p className={'card__text font-arial mt-3'}>{obj.text}</p>
        </div>
    )
}






export default Card


